/* Home.css */

/* Assuming .book-container is the wrapper for book items */
.book-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* Create 3 columns with equal width */
    gap: 20px; /* Add some gap between the book items */
  }
  
  /* .book-item is each individual book element */
  .book-item {
    width: 100%; /* Set the width of each book item to 100% */
    box-sizing: border-box; /* Include padding and border in the element's total width and height */
    cursor: pointer; /* Add a pointer cursor to indicate that it's clickable */
  }
  
  /* .book-item img is the image inside each book item */
  .book-item img {
    width: 100%; /* Make the image fill the container */
    height: auto; /* Maintain the aspect ratio of the image */
    border-radius: 8px; /* Add some border-radius for a rounded appearance */
  }
  