/* styles.css */

body {
    background-color: black;
    margin: 0;
    padding: 0;
    color: white; /* Set the default text color for the page */
  }
  
  header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #292828; /* Set the background color for the header */
    padding: 10px; /* Add some padding to the header */
  }
  
  .header-left,
  .header-middle,
  .header-right {
    display: flex;
    align-items: center;
  }
  
  .header-left img,
  .header-right img {
    margin-right: 10px !important; /* Add !important to override other styles */
  }
  
  .header-middle {
    flex-grow: 1;
    display: flex;
    justify-content: center; /* Center the items horizontally within the .header-middle div */
  }
  
  .header-middle input {
    color: white !important; /* Set the text color to white with !important */
  }
  
  .header-middle button {
    color: white !important; /* Set the text color to white with !important */
    background-color: #292828; /* Set the background color for the button */
  }
  
  .header-middle input::-webkit-input-placeholder {
    color: #292828; /* Set the placeholder text color to #292828 for WebKit browsers */
  }
  
  .header-middle input:-moz-placeholder {
    color: #292828; /* Set the placeholder text color to #292828 for Firefox 18- */
  }
  
  .header-middle input::-moz-placeholder {
    color: #292828; /* Set the placeholder text color to #292828 for Firefox 19+ */
  }
  
  .header-middle input:-ms-input-placeholder {
    color: #292828; /* Set the placeholder text color to #292828 for Microsoft Edge */
  }
  
  .header-middle input::placeholder {
    color: #292828; /* Set the placeholder text color to #292828 for modern browsers */
  }
