/* Add or update the following styles for the book container */
.book-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  
  /* Add or update the following styles for the book items */
  .book-item {
    width: calc(33.33% - 10px); /* Set the width of each book item to approximately 33.33% with some margin */
    margin-bottom: 20px; /* Add some margin at the bottom for spacing */
    box-sizing: border-box; /* Include padding and border in the element's total width and height */
  }
  
  /* Adjust the layout for smaller screens */
  @media (max-width: 768px) {
    .book-item {
      width: 100%; /* Set the width to 100% for smaller screens to stack them vertically */
    }
  }
  
  
  
  